#contactPage{
    min-height: calc(100vh-4rem);
    width: 100vw;
    max-width: 60rem;
    margin: 0 auto;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
#contactPageTitle{
    font-size: 3rem;
    margin-bottom: 2rem;
}
.contactDetails{
    padding: 1rem;
    font-size: medium;
    font-weight: 600;    
    display: flex;    
    align-items: center;    
}
.contacts{
    object-fit: cover;
    width: 3rem;
    height: 3rem;
    margin-right: 1rem;
}
.links{
    display: flex;
    flex-wrap: wrap;
}
.link{
    object-fit: cover;
    height: 3rem;
    width: 3rem;
    margin: 0 1.4rem;
    transition: transform 0.5s;
} 
.link:hover{    
    transform: translateY(-5px);
}