.navbar{
    background: rgb(30,30,30);
    height: 5rem;
    width: 100vw; 
    max-width: 75rem; 
    /* to not strtech the width more than 75 rem */
    margin: 0 auto;
    padding: 1rem 2rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: sticky;
    top: 0;
    z-index: 1;    
}

.logo,.menu{
    object-fit: cover;/*this will let our image from spreading and looking bad it will maintain the ratio*/
    height: 3rem;
    width: 4rem;           
}
.desktopMenuListItem{
    margin: 1rem;
    cursor: pointer;        
    position: relative;
    transition: 0.5s;
}
.desktopMenuListItem:hover{
    padding-bottom: 0.4rem;
    border-bottom: 3px solid yellow;        
}

.desktopMenuBtn{
    background: white;
    color: black;
    border: none;
    border-radius: 1rem;
    padding: 0.75rem;   
}
.desktopMenuImg{
    object-fit: cover;
    height: rem;
    width: 2rem;
    margin-left: 1rem;    
    margin-right: 0rem;
}
.active{
    color: yellow;
    border-bottom: 3px solid yellow;
    padding-bottom: 0.5rem;
}
.mobMenu{
    display: none;
    object-fit: cover;
    height: 1.8rem;
}
.navMenu{
    position: absolute;
    top: 4rem;
    right: 2rem;
    z-index: 2;
    display: flex;
    flex-direction: column;
    padding: 0.5rem;
    height: fit-content;
    min-width: 15rem;
    background: rgb(40,40,40);
    border-radius: 1rem;
}
.listItem{
    color: white;
    padding: 0.5rem 3rem;
    margin: 0.25rem;
    background: rgb(30,30,30);
    cursor: pointer;
}
@media screen and (max-width:720px) {
    .mobMenu{
        display: flex;        
    }
    .desktopMenu{
        display: none;
    }
}