#intro{
    height: calc(100vh-4rem);
    width: 100vw;
    max-width: 75rem;
    margin: 0 auto;
    overflow: hidden;
    position: relative;
}

.bg{
    position: absolute;
    top: -4rem;    
    right: 0;    
    z-index: -1;
    object-fit: cover;
    height: 100vh;   
}

.introContent{
    height: 100vh;
    width: 75vw;
    padding: 2rem;
    font-size: 2rem;
    font-weight: 700;
    display: flex;
    flex-direction: column;        
    justify-content: center;
}
.hello{
    font-size: 1.5rem;
    font-weight: 100;
}
.introName{
    color: yellow;
}
.introPara{
    font-size: small;
    font-weight: 300;
    letter-spacing: 1px;
}
@media screen and (max-width:840px) {
    .bg{
        right: -10vw;            
    }
    .introContent{
        font-size: 8vw;
    }
    .hello{
        font-size: 5vw;
    }
}
@media screen and (max-width:490px) {
    .bg{
        right: -8vw;            
        
    }    
}