#works{
    margin: 0 auto;
    min-height: calc(100vh-3rem);    
    width: 100vw;
    max-width: 60rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    padding: 0 1rem;
    margin-bottom: 4vw;   
}
.worksTitle{
    margin: 1rem 0;
    font-size: 3rem;
}
.worksDesc{
    font-weight: 300;
    font-size: 1rem;
}
.worksImgs{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    max-width: 65rem;    
    margin-top: 3vw;
}
.worksImg1{
    position: relative;
    overflow: hidden;        
    margin-left: 3vw;
}
.worksImg{
    object-fit: cover;
    height: 20rem;
    margin: 0.5rem;  
    width: 100%;      
    display: block; 
}
.layer{
    position: absolute;    
    bottom: 0;
    left: 0;
    background-image: linear-gradient(rgba(0,0,0,0.6),#ff004f);        
    transition: background-color,height 0.4s;        
    border-radius: 6%;
    overflow: hidden;        
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 0;    
}
.worksImg1:hover .layer{   
    height: 100%;
    background-color: lime;
}
.layerTitle{    
    color: yellow;
    font-size: 2vw;
    font-weight: 600;
}
.layerDesc{
    color: yellow;
}
.workLinkImg{
    width: 3vw;
}
.worksBtn{
    margin: 3rem 0;
    padding: 0.5rem 2.5rem;
    border: none;
    background: white;
    border-radius: 2rem;
    font-size: 1rem;
}
@media screen and (max-width:590px) {
    .worksImg{
        height: 60vw;
        margin-top: 2rem;
    }
    
}